import React from 'react';
import {Box, Grid, Typography, Button, makeStyles} from '@material-ui/core';
import {differenceInMinutes, differenceInHours} from 'date-fns';

const useStyles = makeStyles((theme)=>({
    wrapper:{
        border: '1px solid #e8e8e8',
        transition: '0.3s',
        "&:hover":{
        boxShadow: "0px 5px 25px rgba(0,0,0,0.1)",
        borderLeft: "6px solid #379683",
        },
    },
    
    companyName: {
        fontSize: "13.5px",
        backgroundColor: theme.palette.primary.main,
        padding: theme.spacing(0.75),
        borderRadius: "5px",
        display: "inline-block",
        fontWeight: 600,
    },
    skillChip:{
        margin: theme.spacing(0.5),
        padding: theme.spacing(0.75),
        fontSize: "14.5 px",
        borderRadius: "5px",
        transition: ".3s",
        cursor: "pointer",
        fontWeight: 600,
        backgroundColor: theme.palette.secondary.main,
        color: "#fff",

    }
}));

function timeConvert(n) {
    var num = n;
    var hours = (num / 60);
    var rhours = Math.floor(hours);
    var minutes = (hours - rhours) * 60;
    var rminutes = Math.round(minutes);
    return rhours + " hr(s) and " + rminutes + " min(s) ago";
    }

export default (props)=> {
    const classes = useStyles()
    return(
        <Box p={2} className={classes.wrapper}>
            <Grid container alignItems="center">
            <Grid item xs>
                <Typography variant="subtitle1">{props.title}</Typography>
                <Typography className={classes.companyName} variant="subtitle1">{props.companyURL}</Typography>
            </Grid>
            <Grid item container xs>
            {props.skills.map((skill) => (
                <Grid  key={skill} className={classes.skillChip}  item>
                    {skill}
                    </Grid>))}
            </Grid>
            <Grid item container direction="column" alignItems="flex-end" xs>
                <Grid item>
                
                <Typography variant="caption">
                
                    {timeConvert(differenceInMinutes(Date.now(),props.postedOn))} | {props.type} | {props.location}
                    </Typography>
                    
                </Grid>
                <Grid item>
                    <Box mt={2}>
                <Button variant="outlined">Apply</Button>
                </Box>
                </Grid>
                
            </Grid>
            </Grid>
        </Box>
    )
}